import React from 'react';

import { auth } from '../firebase';
const button = {
    display: 'block',
    width: '135px',
    height: '50px',
    background: '#4E9CAF',
    padding: '10px',
    textAlign: 'center',
    color: 'white',
    fontWeight: 'bold',
    borderRadius: '5px',
};
const SignOutButton = () =>
  <button
    type="button" style={button}
    onClick={auth.doSignOut}
  >
    Sign Out
  </button>

export default SignOutButton;


