import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import './App.css';

import SignOutButton from './SignOut';
import * as routes from '../constants/routes';

const Navigation = (props, { authUser }) =>
  <div>
    { authUser
        ? <NavigationAuth />
        : <NavigationNonAuth />
    }
  </div>
Navigation.contextTypes = {
  authUser: PropTypes.object,
};
const button = {
    display: 'block',
    width: '115px',
    height: '25px',
    background: '#4E9CAF',
    padding: '10px',
    textAlign: 'center',
    color: 'white',
    fontWeight: 'bold',
    borderRadius: '5px',
};

const NavigationAuth = () =>
  <ul>
    <li><Link style={button} to={routes.LANDING}>Landing</Link></li>
    <li><Link style={button} to={routes.HOME}>Home</Link></li>
    <li><Link style={button} to={routes.ACCOUNT}>Account</Link></li>
    <li><SignOutButton /></li>
  </ul>

const NavigationNonAuth = () =>
<header>
  <Link style={button} to={routes.LANDING}>Landing</Link> 
  <Link style={button} to={routes.SIGN_IN}>Sign In</Link>
</header>  

export default Navigation;



